html {
  font-size: 23px;
  line-height: 1.3;
  color: #777;
  background: #fff;
  word-wrap: break-word;
  overflow-wrap: break-word; }

:focus {
  outline: none; }

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

button {
  font-family: inherit;
  color: inherit;
  background: inherit;
  border: none;
  text-align: inherit;
  padding: 0; }

p {
  margin: 0; }

input {
  font-family: inherit;
  line-height: 1.3em; }



