.NavbarDropdown {
  max-height: 50vh;
  overflow-y: auto; }

.NavbarDropdown-item {
  line-height: 2rem;
  white-space: nowrap;
  padding: 0 0.8rem; }
  .NavbarDropdown-item.NavbarDropdown-selected {
    background: #ee7f2d;
    color: #fff; }
  .NavbarDropdown-item:hover {
    cursor: pointer;
    background: #ee7f2d;
    color: #fff; }



